import axios from "axios"
export const ADD_CONTACT = "ADD_CONTACT"
export const SET_PAGE = "SET_PAGE"
export const SEND_MESSAGE = "SEND_MESSAGE"

export const updateContactForm = response => ({
  type: ADD_CONTACT,
  response,
})

export const updateMessageForm = response => ({
  type: SEND_MESSAGE,
  response,
})

export const setPage = location => ({
  type: SET_PAGE,
  location,
})

// contact us form with info on project
export const contactUsWithProjectInformation = data => (dispatch, getState) => {
  const { href, pathname } = getState().pageReducer

  const url =
    "https://api.hsforms.com/submissions/v3/integration/submit/5698733/deda0c98-c07c-4d85-a678-8125e225e08e"

  const modifiedData = {
    submittedAt: Date.now(),
    fields: [
      {
        name: "email",
        value: data.email,
      },
      {
        name: "firstname",
        value: data.firstName,
      },
      {
        name: "lastname",
        value: data.lastName,
      },
      {
        name: "projectinfo",
        value: data.yourProject,
      },
      {
        name: "lifecyclestage",
        value: "marketingqualifiedlead",
      },
    ],
    context: {
      pageUri: href,
      pageName: pathname,
    },
  }

  const options = {
    method: "POST",
    headers: { "content-type": "application/json" },
    data: modifiedData,
    url,
  }

  return axios(options)
    .then(response => dispatch(updateContactForm(response)))
    .catch(error => console.log(error))
}

