import { ADD_CONTACT, SET_PAGE, SEND_MESSAGE } from "../actions/forms.js"

const defaultState = {
  testing: "testing",
  hasSubmittedForm: false,
}

const pageReducer = (state = defaultState, action) => {
  switch (action.type) {
    case ADD_CONTACT: {
      return {
        ...state,
        hasSubmittedForm: true,
      }
    }
    case SEND_MESSAGE: {
      return {
        ...state,
        hasSubmittedMessageForm: true,
      }
    }
    case SET_PAGE: {
      return {
        ...state,
        href: action.location.href,
        pathname: action.location.pathname,
      }
    }
    default:
      return state
  }
}

export default pageReducer
